body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    background: #38220f;
}
.Navbar{background:#967259;text-align:left}.Navbar .navbar-brand,.Navbar .nav-link{color:#38220f !important}.Navbar .nav-link:hover{color:#dbc1ac !important}.Navbar .navbar-brand{font-family:"Pacifico",cursive;font-size:1.75em}

.RegisterForm{color:#967259;padding:1rem 0}.RegisterForm.container{max-width:750px}.RegisterForm h1{color:#dbc1ac;font-family:"Open Sans",Helvetica,Arial,sans-serif}.RegisterForm form{text-align:left}.RegisterForm form input{border-radius:3px}.RegisterForm .btn{background:#dbc1ac;color:#38220f;outline:none !important;border:none !important;margin:1.5rem 0}.RegisterForm .btn:hover{background:#967259;color:#38220f}.RegisterForm .btn:focus,.RegisterForm .btn:active{background:#634832 !important;outline:none !important;box-shadow:none !important}

.InfoContainer{text-align:left;margin:2rem auto;color:#dbc1ac}.InfoContainer img{max-height:500px;display:block;margin:auto}.InfoContainer .main-info{background-color:#38220f;color:#dbc1ac}.InfoContainer ul{max-width:650px;margin:auto;font-size:1.25em;font-family:'Open Sans', Helvetica,Arial,sans-serif}.InfoContainer ul li{margin:2rem 0}.InfoContainer .competition-info{max-height:350px;overflow:scroll}@media only screen and (min-width: 992px){.InfoContainer .helper{display:inline-block;height:100%;vertical-align:middle}.InfoContainer img{vertical-align:middle;max-height:500px;display:inline-block;margin:auto}}

.App{text-align:center;background:#38220f;min-height:100vh;height:100%}.App .body{height:100%}

